* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

 /*  #body-wrapper :not(#mobile-menu, #top-nav-bar) {
   filter: brightness(45%); 
    opacity: 0.5;
 }  */ 

.greyed-out {
  filter: brightness(45%);
}

.closed-modal {
  display: none;
}
.open-modal {
  display: block;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

button {
  cursor: pointer;
}

input {
  caret-color: var(--main-orange);
}

body {
  font-family: var(--font);
  background-color: var(--tertiary-grey);
  width: 100%;
  overflow-x: hidden;
}


#body-wrapper {
  width: 100%;
}


:root {
  --main-orange: #d87d4a;
  --secondary-black: #101010;
  --tertiary-grey: #f1f1f1;
  --light-grey: #fafafa;
  --salmon: #fbaf85;
  --pure-white: #fff;
  --pure-black: #000;
  --font: "Manrope", sans-serif;
}

.heading-3 {
  margin-bottom: 5vh;
  margin-top: 15vh;
  font-size: clamp(21px, 3vw, 27px);
  text-transform: uppercase;
}

/* cart modal */

#cart-modal {
  background-color: var(--light-grey);

  padding: 8vh 8vw 0 8vw;
  width: 84vw;
  margin-left: 8vw;
  margin-top: 5vh;
  border-radius: 11px;
}

#cart-heading {
  grid-area: cart-heading;
  font-size: clamp(18px, 3vw, 23px);
  text-transform: uppercase;
}

#remove-all-items {
  grid-area: remove-all;
  background-color: transparent;
  border: none;
  color: gray;
  font-size: clamp(12px, 3vw, 18px);
  width: max-content;
  text-decoration: underline;
  cursor: pointer;
  justify-self: end;
  position: relative;
  left: 10%;
}

.cart-items-flex-group {
  grid-area: items-group;
  display: flex;
  flex-direction: column;
  gap: 3vh;
}

.cart-item-img {
  width: 20vw;
  border-radius: 11px;
}

.cart-item-name-and-price {
  display: flex;
  flex-direction: column;
  gap: 1vh;
  align-self: center;
  position: relative;
  left: 4%;
}

.cart-item-name {
  width: 30vw;
  font-size: clamp(13px, 3vw, 16px);
}

.cart-item-price {
  color: gray;
  font-weight: bold;
}

.cart-item-flex {
  display: flex;
  flex-direction: row;
}

.cart-counter-btn-container {
  display: flex;
  flex-direction: row;
  height: 5vh;
  align-self: center;
}

.cart-in-and-de-btn {
  border: none;
  background-color: #f1f1f1;
  font-weight: bold;
  color: #999;
}


#cart-decrease-btn {
  padding-left: 3vw;
}

#cart-decrease-btn:hover {
  color: var(--main-orange);
}

.cart-number-box {
  border: none;
  background-color: #f1f1f1;
  width: 12vw;
  text-align: center;
  height: inherit;
  font-family: var(--font);
  font-weight: bold;
  font-size: clamp(11px, 3vw, 15px);
}

#cart-increase-btn {
  padding-right: 3vw;
}

#cart-increase-btn:hover {
  color: var(--main-orange);
}

#cart-total-and-price {
  grid-area: cart-total-and-price;
  display: flex;
  flex-direction: row;
  justify-content: start;
}

#cart-total {
  text-transform: uppercase;
  font-size: clamp(13px, 3vw, 18px);
  color: gray;
}

#cart-total-price {
  font-weight: bold;
}

#checkout-btn {
  grid-area: checkout-btn;
  border: none;
  width: 100%;
  background-color: var(--main-orange);
  color: var(--pure-white);
  text-transform: uppercase;
  padding-left: 2vw;
  padding-right: 2vw;
  height: 7vh;
  align-self: center;
  padding-top: 0.5vh;
  font-size: 13px;
  font-weight: normal;
  margin-bottom: 5vh;
}

#checkout-btn:hover {
  background-color: var(--salmon);
}

#checkout-link {
  color: var(--pure-white);
  width: inherit;
  display: block;
  height: inherit;
  position: relative;
  top: 25%;
}


/* nav bar */

#top-nav-bar {
  background-color: var(--secondary-black);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 10vh;
