}

.radio-btn-label {
  padding-left: 8vw;
}

.radio-btn-label .payment-method-inputs {
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  position: relative;
  top: 0;
  left: 0;
  height: 3vh;
  width: 3vh;
  background-color: #eee;
  border-radius: 50%;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.radio-btn-label .payment-method-inputs:checked ~ .checkmark:after {
  display: block;
  background-color: var(--main-orange);
  width: 1.7vh;
  height: 1.7vh;
  position: relative;
  top: 19%;
  left: 19%;
}

.radio-btn-label .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}

.radio-btn-label {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  gap: 5vw;
  position: relative;
}

.radio-btn-label .payment-method-inputs ~ .checkmark {
  background-color: var(--pure-white);
  border: 1px solid #999;
}

.radio-btn-label:hover .payment-method-inputs ~ .checkmark {
