#tick-mark-container {
  border-radius: 50%;
  height: 15vw;
  width: 15vw;
  background-color: var(--main-orange);
  position: relative;
  left: 11%;
  top: 4%;
  margin-top: 4vh;
}

#tick-mark {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  left: 21%;
  top: 8%;
}

#tick-mark::before {
  position: absolute;
  left: 0;
  top: 50%;
  height: 50%;
  width: 3px;
  background-color: var(--pure-white);
  content: "";
  transform: translateX(10px) rotate(-45deg);
  transform-origin: left bottom;
}

#tick-mark::after {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 3px;
  width: 90%;
  background-color: var(--pure-white);
  content: "";
  transform: translateX(10px) rotate(-45deg);
  transform-origin: left bottom;
}

