/* footer styles */

footer {
  background-color: var(--secondary-black);
  display: grid;
  grid-template-areas:
    "orange-bar" "logo" "nav-links" "paragraph" "rights"
    "social-media-links";
  justify-items: center;
  gap: 6vh;
}


#orange-footer-bar {
  grid-area: orange-bar;
  background-color: var(--main-orange);
  height: 1vh;
  width: 20vw;
}


.main-logo-link {
  grid-area: logo;
}

#footer-nav {
  grid-area: nav-links;
}

.footer-link {
  color: var(--tertiary-grey);
  letter-spacing: 0.6vw;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 3;
  text-align: center;
  cursor: pointer;
}

.footer-link:hover {
  color: var(--main-orange);
}

#footer-paragraph {
  grid-area: paragraph;
  color: var(--tertiary-grey);
  width: 80vw;
  text-align: center;
  opacity: 0.6;
  line-height: 2;
  font-size: 13px;
}

#footer-rights {
  grid-area: rights;
  color: var(--tertiary-grey);
  text-align: center;
  opacity: 0.6;
  font-size: 13px;
}

#social-media-links {
  grid-area: social-media-links;
  display: flex;
  width: 40%;
  justify-content: space-around;
  margin-bottom: 5vh;
}

.social-media-icons {
  width: 9vw;
  height: 9vw;
}

.social-media-icons-path:hover {
  fill: var(--main-orange);
  cursor: pointer;
}
